<template>
  <div class="app">
    <MyPageHeader></MyPageHeader>
    <transition name="page"
      enter-active-class="animated bounceIn" leave-active-class="animated bounceOut">
      <router-view></router-view>
    </transition>
    <MyPageFooter></MyPageFooter>
  </div>
</template>

<script>
// add components in other location
import MyPageHeader from './components/MyPageHeader.vue'
import MyPageFooter from './components/MyPageFooter.vue'

export default {
  // enroll local components
  components: {
    MyPageHeader,
    MyPageFooter
  }
}
</script>

<style>
@import "https://cdn.jsdelivr.net/npm/animate.css@3.5.1";
  .Contact {
    position: fixed;
    width: inherit;
  }
  body{
    text-align: center;
  }
</style>



