import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/MyPageBody'

Vue.use(VueRouter)

const routes = [
  { path: '/', component: Home },
  { path: '/Home', component: Home },
  { path: '/MyPage', component: Home }
]

export const router = new VueRouter({
  mode: 'history',
  routes
})



